___CSS_LOADER_EXPORT___.push([module.id, `/* Responsive navbar menu */
@media only screen and (max-width: 600px) {
    #menuIcon {
        display: block;
    }
    .navbar-menu {
        flex-direction: column;
        text-align: center;
        display: none;
        width: 100%;
        position: absolute;
        top: 60px;
        left: 0;
        background-color: #218284;
    }
    #drawer.open {
        display: flex;
        margin-top: 12px;
    }
    #drawer li a {
        display: block;
        padding: 10px;
    }
}

/* Responsive styles for cultural list */
@media only screen and (max-width: 600px) {
    #cultural-list {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .cultural-card {
        margin: 0.5rem auto;
        max-width: 100%;
    }
}

/* Responsive styles for recommendation cards */
@media only screen and (max-width: 600px) {
    .rekom-card {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    .card-item, .card-rekom {
        margin: 0.5rem auto;
        max-width: 100%;
    }
}

/* Medium screens */
@media only screen and (min-width: 601px) and (max-width: 1024px) {
    .navbar-menu {
        display: flex;
        justify-content: center; 
        list-style: none; 
        padding: 0; 
        margin: 0; 
    }
    
    .navbar-menu li {
        padding: 0 10px; 
    }
    
    #cultural-list {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
    .cultural-card {
        margin: 1rem;
    }
    .rekom-card {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
    .card-item, .card-rekom {
        margin: 1rem;
    }
}

/* Large screens */
@media only screen and (min-width: 1025px) {
    .navbar-menu {
        display: flex; 
        justify-content: center; 
        list-style: none; 
        padding: 0; 
        margin: 0; 
    }
    
    .navbar-menu li {
        padding: 0 10px; 
    }
    #cultural-list {
        grid-template-columns: repeat(3, 1fr);
        gap: 1rem;
    }
    .cultural-card {
        margin: 1rem;
    }
    .rekom-card {
        grid-template-columns: repeat(3, 1fr);
        gap: 1rem;
    }
    .card-item, .card-rekom {
        margin: 1rem;
    }
}

/* Common styling for card items and recommendation cards */
.card-item, .card-rekom {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s;
}

.card-item:hover, .card-rekom:hover {
    transform: scale(1.05);
}

.card-item img.article-image, .card-rekom img.article-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-bottom: 1px solid #ddd;
}

.card-item h1, .card-rekom h1 {
    font-size: 1.5em;
    margin: 16px;
    color: #333;
}

.card-item p, .card-rekom p {
    font-size: 1em;
    margin: 0 16px 16px;
    color: #666;
}

/* Responsive styles for Madura image on mobile */
@media only screen and (max-width: 600px) {
    .madura-image-container {
        grid-column: 1 / -1; 
    }
  
    .madura-image {
        width: 100%; 
        max-width: none; 
        height: auto;
    }
}

/* Responsive styles for intro text */
@media only screen and (max-width: 600px) {
    .intro-container {
        display: block; 
    }

    .intro-text {
        text-align: center; 
    }
}

/* Small screens */
@media only screen and (max-width: 600px) {
    .team-group {
        grid-template-columns: repeat(1, 1fr); 
    }
}

/* Medium screens */
@media only screen and (min-width: 601px) and (max-width: 1024px) {
    .team-group {
        grid-template-columns: repeat(2, 1fr);
    }
    .madura-image-container {
        grid-column: 1 / -1; 
    }
}

/* Large screens */
@media only screen and (min-width: 1025px) {
    .team-group {
        grid-template-columns: repeat(3, 1fr); 
    }
}


/* Media Queries untuk responsivitas */
@media (max-width: 1200px) {
    .heroTitle {
        font-size: 2.2em;
    }
    .heroTagline {
        font-size: 1.1em;
    }
}

@media (max-width: 992px) {
    .heroTitle {
        font-size: 2em;
    }
    .heroTagline {
        font-size: 1em;
    }
}

@media (max-width: 768px) {
    .hero {
        min-height: 300px;
        padding: 15px;
    }
    .heroTitle {
        font-size: 1.8em;
    }
    .heroTagline {
        font-size: 0.9em;
    }
}

@media (max-width: 576px) {
    .hero {
        min-height: 250px;
        padding: 10px;
    }
    .heroTitle {
        font-size: 1.5em;
    }
    .heroTagline {
        font-size: 0.8em;
    }
}

@media only screen and (min-width: 768px) {
    .search-container {
      width: 100%; 
      max-width: 1000px; 
      margin: 0 auto; 
      margin-bottom: 15px;
    }

    .cultural-info  {
        width: 95%;
        margin-top: -10px; 
        font-size: 17px;
        margin-left: auto;
        margin-right: auto;
    }
  
    #search-input {
      width: 100%; 
      max-width: 1000px; 
    }
  
    #search-button {
      padding: 10px 20px; 
      font-size: 16px;
    }
  }
  
  @media only screen and (max-width: 767px) {
    .search-container {
      width: 90%; 
      margin: 0 auto; 
    }

    .cultural-info h2 {
        font-size: 25px;
        width: 95%;
        margin: 0 auto; 
        display: block;
        margin-top: -10px; 
        text-align: center;
        margin-bottom: 15px;
    }
    
  
    #search-input {
      width: 100%; 
      max-width: 400px; 
    }
  
    #search-button {
      padding: 8px 16px; 
      font-size: 14px;
    }
  }
  
  /* Untuk layar dengan lebar kurang dari atau sama dengan 768px */
@media only screen and (max-width: 768px) {
    .donate-container {
        width: 90%;
    }

    .donate-container h2 {
        font-size: 30px; 
    }

    .donate-container p {
        font-size: 20px; 
    }

    .qr-code img {
        max-width: 70%; 
    }
}

/* Untuk layar dengan lebar lebih dari 768px */
@media only screen and (min-width: 769px) {
    .donate-container {
        width: 70%; 
    }
}
