/* Setel dasar HTML dan body */
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    display: flex;
    flex-direction: column;
}

/* Gaya untuk skip-link */
.skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background: rgba(33, 130, 132, 0.8);
    color: white;
    padding: 8px 16px;
    z-index: 100;
    transition: top 0.3s;
    text-decoration: none;
}

.skip-link:focus {
    top: 10px;
}

/* Gaya untuk navigasi */
.nav {
    background-color: #218284;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    font-size: 16px;
}

  
.navbar-menu {
    display: flex; 
    justify-content: center; 
    list-style: none;
    padding: 0; 
}

.navbar-menu li {
    padding: 0 15px; 
}


.navbar-menu a {
    text-decoration: none;
    color: white;
    font-size: 25px; 
}

.navbar-menu a:hover {
    color: #c7c7c7;
}

.logo {
    width: 80px;
    height: 80px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

/* Gaya untuk menu icon */
#menuIcon {
    display: none; 
    background-color: transparent; 
    border: none;  
    color: white;  
    font-size: 24px;  
    cursor: pointer;  
    padding: 10px;  
}

.navbar-menu.open {
    margin-top: 50px;
}

button, .navbar-menu a, input[type="text"], textarea {
    min-width: 44px;  
    min-height: 44px;
    padding: 10px;    
    font-size: 16px;  
    display: flex;
    align-items: center;
    justify-content: center;
}

#menuIcon {
    width: 44px;
    height: 44px;
}

/* Gaya untuk hero section */
.hero {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 380px;
    width: 100%;
