    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 auto;
    padding: 20px;
}

.heroInner {
    margin: 0 auto;
    max-width: 960px;
    padding: 20px;
}

.heroTitle {
    font-size: 2.5em;
    color: #ffffff;
    margin-bottom: 0.5em;
}

.heroTagline {
    font-size: 1.2em;
    color: #d0d0d0;
}

.intro {
    margin-bottom: 20px;
    grid-column: 1 / 2;
}
  
.intro-title {
    font-size: 40px;
    color: #218284;
    margin-bottom: 10px;
    text-align: center;
    margin-top: 10px;
