}

.qr-code {
    display: flex; 
    flex-direction: column; 
}

.qr-code img {
    max-width: 40%; 
    height: 40%; 
    display: block; 
    margin: 0 auto 10px; 
    margin-top: -4px; 
    border-radius: 8px; 
    border: 2px solid #ccc; 
}

.qr-code p {
    margin: 0; 
    margin-top: 0px; 
    margin-bottom: 10px; 
    font-weight: 600;
    align-items: left;
    font-size: 20px;
}

.qr-code h3 {
    margin-top: -5px;
    font-size: 22px;
    margin-bottom: 15px;
}

.no-results {
    font-size: 1.2em;
    color: #218284; /* Warna teks merah */
    text-align: center;
    margin-top: 10px;
    font-weight: bold;
  }
  
/* Gaya untuk footer */
footer {
    background-color: #218284;
    color: #ffffff;
    text-align: center;
    padding: 10px 10px;
    font-size: 16px;
