B;EACnB;;AAEF,sBAAsB;AACtB;IACI,yBAAyB;IACzB,cAAc;IACd,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;AACnB","sourcesContent":["*, *:before, *:after {\r\n    box-sizing: border-box;\r\n}\r\n\r\n/* Setel dasar HTML dan body */\r\nhtml, body {\r\n    margin: 0;\r\n    padding: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    font-family: 'Lato', sans-serif;\r\n    font-weight: bold;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n/* Gaya untuk skip-link */\r\n.skip-link {\r\n    position: absolute;\r\n    top: -40px;\r\n    left: 0;\r\n    background: rgba(33, 130, 132, 0.8);\r\n    color: white;\r\n    padding: 8px 16px;\r\n    z-index: 100;\r\n    transition: top 0.3s;\r\n    text-decoration: none;\r\n}\r\n\r\n.skip-link:focus {\r\n    top: 10px;\r\n}\r\n\r\n/* Gaya untuk navigasi */\r\n.nav {\r\n    background-color: #218284;\r\n    color: white;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding: 5px 10px;\r\n    font-size: 16px;\r\n}\r\n\r\n  \r\n.navbar-menu {\r\n    display: flex; \r\n    justify-content: center; \r\n    list-style: none;\r\n    padding: 0; \r\n}\r\n\r\n.navbar-menu li {\r\n    padding: 0 15px; \r\n}\r\n\r\n\r\n.navbar-menu a {\r\n    text-decoration: none;\r\n    color: white;\r\n    font-size: 25px; \r\n}\r\n\r\n.navbar-menu a:hover {\r\n    color: #c7c7c7;\r\n}\r\n\r\n.logo {\r\n    width: 80px;\r\n    height: 80px;\r\n    background-image: url('../public/images/logo-budra.png');\r\n    background-size: contain;\r\n    background-repeat: no-repeat;\r\n    background-position: center;\r\n}\r\n\r\n/* Gaya untuk menu icon */\r\n#menuIcon {\r\n    display: none; \r\n    background-color: transparent; \r\n    border: none;  \r\n    color: white;  \r\n    font-size: 24px;  \r\n    cursor: pointer;  \r\n    padding: 10px;  \r\n}\r\n\r\n.navbar-menu.open {\r\n    margin-top: 50px;\r\n}\r\n\r\nbutton, .navbar-menu a, input[type=\"text\"], textarea {\r\n    min-width: 44px;  \r\n    min-height: 44px;\r\n    padding: 10px;    \r\n    font-size: 16px;  \r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n#menuIcon {\r\n    width: 44px;\r\n    height: 44px;\r\n}\r\n\r\n/* Gaya untuk hero section */\r\n.hero {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    min-height: 380px;\r\n    width: 100%;\r\n    text-align: center;\r\n    background-image: url('../public/images/heros/hero-image.jpg');\r\n    background-size: cover;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n    margin: 0 auto;\r\n    padding: 20px;\r\n}\r\n\r\n.heroInner {\r\n    margin: 0 auto;\r\n    max-width: 960px;\r\n    padding: 20px;\r\n}\r\n\r\n.heroTitle {\r\n    font-size: 2.5em;\r\n    color: #ffffff;\r\n    margin-bottom: 0.5em;\r\n}\r\n\r\n.heroTagline {\r\n    font-size: 1.2em;\r\n    color: #d0d0d0;\r\n}\r\n\r\n.intro {\r\n    margin-bottom: 20px;\r\n    grid-column: 1 / 2;\r\n}\r\n  \r\n.intro-title {\r\n    font-size: 40px;\r\n    color: #218284;\r\n    margin-bottom: 10px;\r\n    text-align: center;\r\n    margin-top: 10px;\r\n    grid-column: 1 / 3; \r\n}\r\n  \r\n.intro-text p {\r\n    font-size: 25px;\r\n    color: #000;\r\n    text-align: justify;\r\n    margin-top: 10px;\r\n    font-weight: 500;\r\n}\r\n  \r\n.madura-image-container {\r\n    grid-column: 2 / 3;\r\n}\r\n  \r\n.madura-image {\r\n    max-width: 100%;\r\n    height: auto;\r\n    border-radius: 10px;\r\n    margin-top: 10px;\r\n}\r\n\r\n/* Gaya untuk container */\r\n.container {\r\n    width: 950%;\r\n    max-width: 100%;\r\n    margin: 0 auto;\r\n    padding: 20px;\r\n    background-color:z #ffffff;\r\n    box-shadow: 0 2px 5px rgba(0,0,0,0.1);\r\n    text-align: center;\r\n    flex: 1;\r\n    border-radius: 10px;\r\n}\r\n\r\n.intro-container {\r\n    display: grid;\r\n    grid-template-columns: 1fr 1fr;\r\n    gap: 20px;\r\n    border-radius: 10px;\r\n    background-color: white;\r\n    padding: 20px;\r