olor: white;\r\n    text-decoration: none;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.card-item .detail-link:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n.card-item:hover {\r\n    transition: background-color 0.3s ease; \r\n}\r\n\r\n.card-content .detail-link {\r\n    color: white;\r\n    text-decoration: none;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.card-content .detail-link:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n.card-c.card-content {\r\n    transition: background-color 0.3s ease;\r\n}\r\n\r\n/* CSS Grid for recommendation cards */\r\n.rekom-card {\r\n    display: grid;\r\n    grid-template-columns: repeat(3, 1fr);\r\n    gap: 1rem;\r\n    padding: 1rem;\r\n    font-size: 16px;\r\n}\r\n\r\n.card-item img {\r\n    width: 100%;\r\n    height: 200px;\r\n    object-fit: cover;\r\n    border-bottom: 1px solid #ddd;\r\n    margin-bottom: 1rem;\r\n}\r\n\r\n.rekom-card .card-item h1 {\r\n    font-size: 1.2rem;\r\n    margin: 0.5rem 0;\r\n    color: #218284 !important;\r\n}\r\n\r\n.rekom-card .card-item p {\r\n    color: #000; \r\n    margin-top: 0px;\r\n    text-align: justify;\r\n    font-weight: 500;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.detail-link {\r\n    display: inline-block;\r\n    padding: 7px 14px;\r\n    margin-top: 5px;\r\n    font-size: 12px;\r\n    background-color: #218284;\r\n    color: #fff;\r\n    text-decoration: none;\r\n    border-radius: 4px;\r\n    transition: background-color 0.3s;\r\n}\r\n\r\n.detail-container {\r\n   width: 90%;\r\n    gap: 20px;\r\n    border-radius: 10px;\r\n    background-color: white;\r\n    padding: 20px;\r\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\r\n    align-items: center; \r\n    margin: 0 auto; \r\n    display: contents; \r\n  }\r\n\r\n  .cultural-detail img {\r\n    width: 80%;\r\n    height: 50%; \r\n    grid-column: span 1;\r\n    margin: 0 auto; \r\n    border-radius: 10px;\r\n  }\r\n  \r\n  .detail-content h3 {\r\n    font-size: 30px;\r\n    width: 80%;\r\n    text-align: justify;\r